const textItems = [
  {
    text: '      ,         .',
  },
  // {text: '      ,    "    "'}
];

const textEl = document.querySelector('.section__text');

const mateText = () => {
  const element = textItems
    .map(({ text }) => {
      return `    
        <p class="lng-text">${text}</p>`;
    })
    .join('');
  textEl.insertAdjacentHTML('beforeend', element);
};
mateText();
